import React from "react";
import { useStoreon } from "storeon/react";
import ProductCard from './ProductCard';
import { defaultProductCard } from '../../images'
import { useLocation, useNavigate } from "react-router-dom";

const ProductCardContainer = ({
    key,
    id,
    url,
    title,
    role,
    brand,
    prices,
    stock,
    colors,
    sizes,
    images,
    isSales,
    isNew,
    isHit,
    is_liked,
    product_rc,
    article,
    currency,
    disabledHover,
    isLoadingMyWish = false,
    ...props
}) => {

    const { dispatch } = useStoreon();
    const location = useLocation();
    const navigate = useNavigate();
    const defaultImageSet = [defaultProductCard];

    const handlerClick = (url) => {
      const dataProduct = {
        id: id,
        title: title,
        category: "",
        brand: brand,
        slug: "",
        minimum_rc: null,
        minimum_rc_price: null,
        media: [...images],
        colors: colors,
        sizes: [...sizes],
        collections: [],
        is_new: isNew,
        is_bestseller: isHit,
        is_closeout: isSales,
        is_in_stock: stock,
        is_liked: is_liked,
        in_cart_count: 0,
        in_stock_count: 0,
        prices: prices,
        product_rc: product_rc,
        is_collection: false,
        product_sku: [],
        content: "",
        extra: "",
        short_content: "",
        created_at: "",
        updated_at: "",
        ordering: 0,
        review: {
            all_count: 0,
            all_count_percent: 0
        },
        seo_title: "",
        seo_keywords: "",
        seo_description: "",
        seo_author: "",
        seo_og_type: "website",
        seo_image: null,
        article: article,
        product_url: "",
        isLoadingProductData: true
      }
    //   dispatch('setDataProductDetailsStore',dataProduct)
      navigate(`/${url}`,{
      state: dataProduct
      })
    };
    const addLikeProductCard = (id) => {
        const params = {
            id: id,
            whereLike: 'product',
            pathname: location.pathname,
            redirectTo: (path) => {
                const timerTimeout = setTimeout(() => {
                    navigate(path);
                    return () => clearTimeout(timerTimeout);
                }, 500)
            }
        }
        dispatch('addWishList', params)
    }

    const removeLikeProductCard = (id) => {
        const params = {
            id: id,
            whereLike: 'product',
            pathname: location.pathname,
            redirectTo: (path) => {
                const timerTimeout = setTimeout(() => {
                    navigate(path);
                    return () => clearTimeout(timerTimeout);
                }, 500)
            }
        }
        dispatch('removeWishList', params)
    }

    const handleQuickView = (id, url) => {
        const params = {
            id: id,
            url: url
        }
        dispatch('quickViewProduct', params);
    }
    return (
        <ProductCard
            id={id}
            key={key}
            url={url}
            role={role}
            brand={brand}
            prices={prices}
            stock={stock}
            title={title}
            sizes={sizes}
            images={images?.length ? images : defaultImageSet}
            isNew={isNew}
            isHit={isHit}
            colors={colors}
            isSales={isSales}
            is_liked={is_liked}
            article={article}
            currency={currency}
            product_rc={product_rc}
            isLoadingMyWish={isLoadingMyWish}
            disabledHover={disabledHover}
            handlerClick = {handlerClick}
            handleQuickView={handleQuickView}
            addLikeProductCard={addLikeProductCard}
            removeLikeProductCard={removeLikeProductCard}
        />
    )
}

export default ProductCardContainer;