import React from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';

import AsyncComponent from '../../helpers/asyncComponent';
import Text from '../../helpers/Text';
import Button from '../Button';
import Icon from '../Icon';
import {
  productDiscount,
  productNew,
  productHit,
  favoriteIcon,
  favoriteIconNew,
  favoriteFilledIcon,
  defaultImageCard,
} from '../../images';

import style from './productCard.module.scss';
import { ROLE } from '../../const';

const AsyncSlider = AsyncComponent(() => {
  return import('./Slider');
});

const ProductCard = (props) => {
  const {
    swapperDisabled,
    disabledHover,
    isLoadingMyWish,
    title,
    role,
    id,
    url,
    brand,
    prices,
    stock,
    colors,
    images = [],
    isSales,
    isNew,
    isHit,
    sizes,
    currency,
    is_liked,
    handlerClick,
    handleQuickView,
    addLikeProductCard,
    removeLikeProductCard,
  } = props;
  return (
    <div       
      className={[style['product-card']]}
    >
        <div className={style['product-card__wrap']}>
        <div className={style['product-card__top']}>
          <div className={'product_card-hide_hydrate'}>
            <div className={style['product-card__tags']}>
              {isSales ? (
                <div className={style['product-card__tag']}>
                  <img
                    width={'20px'}
                    height={'20px'}
                    src={productDiscount}
                    alt={Text({ text: 'sale' })}
                  />
                </div>
              ) : null}
              {isNew ? (
                <div className={style['product-card__tag']}>
                  <img
                    width={'20px'}
                    height={'20px'}
                    src={productNew}
                    alt={Text({ text: 'new' })}
                  />
                </div>
              ) : null}
              {isHit ? (
                <div className={style['product-card__tag']}>
                  <img
                    width={'20px'}
                    height={'20px'}
                    src={productHit}
                    alt={Text({ text: 'hit' })}
                  />
                </div>
              ) : null}
            </div>
          </div>

          {stock ? (
            <div className={style['product-card__stock']}>{Text({ text: 'inStock' })}</div>
          ) : (
            ''
          )}
          {swapperDisabled ? (
            <img src={images[0]} alt={'default image'} />
          ) : (
            <AsyncSlider 
              url={url} 
              images={images} 
              id={id}
              handlerClick={handlerClick}
             />
          )}
          <div className={style['product-card__head']}>
            {
              role !== ROLE.UNREGISTRED && role !== ROLE.RETAIL? 
                <h6 className={style['product-card__brand']}>{brand}</h6>
                : null
            }
            <Button
              variant="text"
              disabled={isLoadingMyWish}
              className={classNames({
                [style['product-card__favorite']]: true,
              })}
              
            >
              <div
              >
                <Icon 
                  disabled={isLoadingMyWish}                  
                  src={is_liked ? favoriteFilledIcon : favoriteIcon} height={20} width={20}
                  onClick={() => {
                    is_liked?
                    removeLikeProductCard(id)
                    : addLikeProductCard(id);
                  }}
                />
              </div>
             
            </Button>
          </div>
          <h5 className={style['product-card__name']}>
            <Link to={`/${url}`} 
              state = { {
                test:'text test1'
              } }
            >{title}</Link>
          </h5>
          <div className={style['product-card__prices']}>
            <div
              className={classNames({
                [style['product-card__price']]: true,
                [style['product-card__price--new']]: prices?.old_price,
              })}
            >
              {prices.price} {currency}
            </div>
            {prices?.old_price && role !== ROLE.RETAIL && role !== ROLE.UNREGISTRED ? (
              <div className={style['product-card__price--old']}>
                {prices?.old_price} {currency}
              </div>
            ) : null}
          </div>
        </div>

        <div className={style['product-card__bottom--sizes']}>
          <ul className={style['product-card__sizes']}>
            {sizes.map((el, i) => {
              return (
                <li
                  key={i}
                  className={style['product-card__sizes--item']}
                >{el.title}</li>
              );
            })}
          </ul>
        </div>
       {
        disabledHover?
          <ul className={style['product-card__colors']}>
          {colors.map((el, i) => {
            return (
              <li
                key={i}
                className={style['product-card__colors--item']}
                style={{ backgroundColor: el.color }}
              ></li>
            );
          })}
        </ul>
        :<div className={style['product-card__bottom']}>
          <ul className={style['product-card__colors']}>
            {colors.map((el, i) => {
              return (
                <li
                  key={i}
                  className={style['product-card__colors--item']}
                  style={{ backgroundColor: el.color }}
                ></li>
              );
            })}
          </ul>
          <span onClick={()=>handleQuickView(id, url)} className={style['product-card__link']}>
            <Text text={'quickView'} />
          </span>
        </div>
       }

       
        
      </div>
    </div>
  );
};

export default ProductCard;
